<template>
  <div>
    <button @click="send"></button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      path: 'ws://localhost:9978/',
      socket: null
    }
  },
  mounted () {
    // 
    this.init()
  },
  methods: {
    init: function () {
      if (typeof (WebSocket) === 'undefined') {
        alert('socket')
      } else {
        // socket
        this.socket = new WebSocket(this.path)
        // socket
        this.socket.onopen = this.open
        // socket
        this.socket.onerror = this.error
        // socket
        this.socket.onmessage = this.getMessage
      }
    },
    open: function () {
      console.log('socket')
    },
    error: function () {
      console.log('')
    },
    getMessage: function (msg) {
      console.log(msg.data)
      alert(msg.data)
    },
    send: function () {
      this.socket.send('aaaaaaa1')
    },
    close: function () {
      console.log('socket')
    }
  },
  destroyed () {
    // 
    this.socket.onclose = this.close
  }
}
</script>

<style>

</style>



