import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/Home'
import Index from '@/components/Index'
import Login from '@/components/Login'
import MyElement from '@/components/MyElement'
import ElementIndex from '@/components/ElementIndex'
import WebSocketIndex from '@/components/WebSocketIndex'
import SockjsIndex from '@/components/SockjsIndex'
Vue.use(Router)

export default new Router({
  mode: 'hash', // # 
  routes: [
    {
      path: '/',
      // name: 'HelloWorld',
      // component: HelloWorld,
      // meta: {
      //   title: 'Hello World'
      // }
      name: 'SockjsIndex',
      component: SockjsIndex,
      meta: {
        title: 'SockjsIndex'
      }
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      meta: {
        title: ''
      }
    },
    {
      path: '/index',
      name: 'Index',
      component: Index,
      meta: {
        title: ''
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {
        title: ''
      }
    },
    {
      path: '/myElement',
      name: 'MyElement',
      component: MyElement,
      meta: {
        title: 'element'
      }
    },
    {
      path: '/elementIndex',
      name: 'ElementIndex',
      component: ElementIndex,
      meta: {
        title: 'elementIndex'
      }
    },
    {
      path: '/webSocketIndex',
      name: 'WebSocketIndex',
      component: WebSocketIndex,
      meta: {
        title: 'webSocketIndex'
      }
    },
    {
      path: '/sockjsIndex',
      name: 'SockjsIndex',
      component: SockjsIndex,
      meta: {
        title: 'SockjsIndex'
      }
    }
  ]
})



