<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div>
      <div style="height: 300px;width: 300px;float: left">
        <h2>h2</h2>
        <h3>h3</h3>
        <button v-on:click="qrcode"></button><br/>
        <button v-on:click="tiaozhuan">Ajax</button><br/>
        <button v-on:click="tiaozhuanIndex">Index</button><br/>
        <button v-on:click="tiaozhuanElement">Element</button><br/>
        <img src="../images/head.jpg" width="100" height="100" /><br/>
        <a href="https://www.baidu.com"></a>
      </div>
      <div style="height: 300px;width: 200px;float: left">
        <h4 style="color: firebrick">:</h4>
        <ol>
          <li v-for="todo in todos" v-bind:key="todo.text">
            {{ todo.text }}
          </li>
        </ol>
      </div>
      <!--border: black 2px solid;-->
      <div style="height: 300px;width: 200px;float: left">
        <h4 style="color: firebrick">:</h4>
          <ul>
            <li>Coffee</li>
            <li>Tea</li>
            <li>Milk</li>
          </ul>
      </div>
      <div style="height: 300px;width: 300px;float: left">
        <h4 style="color: firebrick">:</h4>
        <table style="height: 100px; width: 250px;border-collapse: collapse" border="2" >
          <tr align="center">
            <th></th>
            <th></th>
          </tr>
          <tr align="center">
            <td>wuzz</td>
            <td>11</td>
          </tr>
          <tr align="center">
            <td>jack</td>
            <td>22</td>
          </tr>
        </table>
      </div>
      <div style="height: 300px;width: 340px;float: left">
        <h4 style="color: firebrick">:</h4>
        <form>
          : <input type="text" name="firstname" v-model="modelPassworld">{{modelPassworld}}<br>
          : <input type="password" name="pwd"><br>
          
          <input type="radio" name="sex" value="male">Male
          <input type="radio" name="sex" value="female">Female<br>
          
          <input type="checkbox" name="vehicle" value="Bike">I have a bike
          <input type="checkbox" name="vehicle" value="Car">I have a car<br>
          Username: <input type="text" name="user"><br>
          
          <input type="submit" value="Submit">
        </form>
      </div>
    </div>
    <div id="app-2" v-if="seen" style="height: 300px;width: 560px;float: left">
      <h4 style="color: firebrick"></h4>
      <button v-on:click="clickTest"></button>
      <button v-on:click="reverseMessage"></button>
      <span v-bind:title="message">
        
      </span><br/>
      <span v-once>: {{ message }}</span><br/>
      <p>Using v-html directive: <span v-html="rawHtml"></span></p>
      <h3>{{computeFiled}}</h3>
    </div>
    <div id="app-3" v-if="seen" style="height: 300px;width: 200px;float: left">
      <h4 style="color: firebrick">Class  Style </h4>
      <div class="static"
           v-bind:class="{ active: isActive, 'text-danger': hasError }">class-style</div>
      <div style="margin-top: 10px"
           v-bind:class="[{ active: isActive }, errorClass]">class-style array</div>
    </div>
    <div id="app-4" v-if="seen" style="height: 300px;width: 600px;float: left">
      <h4 style="color: firebrick"></h4>
      <button @click="awesomeTest(message,seen)"></button>
      <!--  -->
      <button v-on:click.once="awesomeTest(message,seen)"></button>
      <h1 v-if="awesome">Vue is awesome!</h1>
      <h1 v-else>Oh no </h1>
    </div>
    <img :src = "qrcodeImage" >
    <img src = "http://localhost:8889/qrcode" >
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      message: ' ' + new Date().toLocaleString(),
      seen: true,
      todos: [],
      modelPassworld: '',
      rawHtml: '<span style="color: red">aaaa</span>',
      isActive: true,
      hasError: true,
      errorClass: 'text-danger',
      awesome: false,
      qrcodeImage: ''
    }
  },
  methods: {
    qrcode: function () {
      this.qrcodeImage = null
      this.$axios({
        method: 'get',
        url: '/api/qrcode',
        responseType: 'arraybuffer'
      }).then(function (res) {
        return 'data:image/png;base64,' + btoa(
          new Uint8Array(res.data)
            .reduce((data, byte) => data + String.fromCharCode(byte), '')
        )
      }).then(data => {
        this.qrcodeImage = data
      }).catch(function (err) {
        alert(err)
      })
    },
    tiaozhuan: function () {
      this.$router.push('/home')
    },
    tiaozhuanIndex: function () {
      this.$router.push('/login')
    },
    tiaozhuanElement: function () {
      this.$router.push('/myElement')
    },
    clickTest: function () {
      this.message = '22222222222'
      // this.seen = false
      this.todos = [
        { text: ' JavaScript' },
        { text: ' Vue' },
        { text: '' }
      ]
      this.rawHtml = '<span style="color: red">This should be red</span>'
    },
    reverseMessage: function () {
      this.msg = this.msg.split('').reverse().join('')
    },
    awesomeTest: function (message, seen) {
      alert(message + '  ' + seen)
      this.awesome ? this.awesome = false : this.awesome = true
    }
  },
  computed: {
    //  getter
    computeFiled: function () {
      // `this`  vm 
      return this.msg + ' Wuzz'
    }
  },
  watch: {// 
    msg: function (newMsg, oldMsg) {
      alert(oldMsg + '==============>' + newMsg)
    }
  }, // vue
  beforeCreate: function () {
    console.group('------beforeCreate------')
    console.log(this.todos)
  },
  created: function () {
    console.group('------created------')
    console.log(this.todos)
  },
  beforeMount: function () {
    console.group('------beforeMount------')
    console.log(this.todos)
  },
  mounted: function () {
    console.group('------mounted ------')
  },
  beforeUpdate: function () {
    console.group('beforeUpdate ===============')
  },
  updated: function () {
    console.group('updated ===============')
  },
  beforeDestroy: function () {
    console.group('beforeDestroy ===============')
  },
  destroyed: function () {
    console.group('destroyed ===============')
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



